<template>
<div>
    <TopNav v-show="!this.$root.playingMusic.isFullscreenShow"></TopNav>
    <form @submit.stop.prevent="searchList(keywords)" v-show="!$root.playingMusic.isFullscreenShow">
        <div class="search">
            <i></i>
            <input type="text" v-model="keywords" placeholder="搜索歌曲、歌手、专辑">
            <i @click="clearSearch"></i>
        </div>
    </form>
    <HotSearch :hotSearch="hotSearch" v-show="keywords==''" @clickHotSearch="clickHotSearch"></HotSearch>
    <MusicItem v-show="isClickHotSearch && keywords!=''" :MusicList="hotSearchResult"></MusicItem>
    <Loading v-show="searching"></Loading>
    <Suggest :suggests="suggests" v-show="showSearch && !isClickHotSearch" :keywords="keywords" @searchResult="searchList"></Suggest>
    <SearchResult v-show="showList && !$root.playingMusic.isFullscreenShow" :searchMusicList="searchMusicList"></SearchResult>
</div>
</template>

<script>
import TopNav from '../components/TopNav'
import Suggest from  "../components/Suggest"
import Loading from '../components/Loading'
import SearchResult from "../components/SearchResult"
import HotSearch from '../components/HotSearch'
import MusicItem from '../components/MusicItem'
export default {
    data(){
        return {
            keywords:'',
            suggests:[],
            searchMusicList:[],
            searching:false, //是否显示 加载loading
            showList:false,     //是否显示搜索结果
            showSearch:false,    //是否显示搜索提示
            hotSearch:[],        //热门搜索
            isClickHotSearch:false, //是否点击热门搜索
            hotSearchResult:[]//热门搜索结果
        }
    },
    methods:{
        searchList(keywords){
            this.showList = false;
            this.searching = false;
            this.showSearch = false;
            //初始化
            this.searchMusicList = [];
            this.suggests = [];
            this.axios.get('/search?keywords='+keywords).then(d=>{
                if(d.data.code == 200){
                    this.showList = true;
                    this.searchMusicList=d.data.result.songs.map(dat=>{
                        return {
                            name:dat.name,
                            id:dat.id,
                            song:{
                                artists:dat.artists,
                                privilege:{
                                    maxbr:1
                                }
                            }
                        }
                    });
                }
            }).catch(()=>{
                console.log("请求搜索失败");
            });
        },
        playMusic(id){
            this.$emit('playMusic',id);
        },
        clearSearch(){
            this.keywords="";
        },
        clickHotSearch(searchName){
            this.keywords = searchName;
            this.isClickHotSearch = true;
            this.$http.get('/search/suggest?keywords='+searchName).then(data=>{
                this.hotSearchResult = data.data.result.songs.map(v=>{
                    return {
                        id:v.id,
                        name:v.name,
                        song:{
                            privilege:{
                                maxbr:v.mark
                            },
                            artists:v.artists
                        }
                    }
                });
            }).catch(()=>{
                console.log("请求热门搜索失败");
            })
            
        }
    },
    watch:{
        keywords(value){
            if(!this.isClickHotSearch){
                //将数据重置
                this.suggests=[];
                this.searching=true;
                this.showList=false;
                this.$http.get('/search/suggest?keywords='+value).then(data=>{
                    if(data.data.code == 200 && data.data.result.order ){
                        let result = data.data.result;
                        let re = result.order.reduce((prev,v)=>{
                            return prev.concat(result[v])
                        },[]);
                        this.showSearch=true;
                        this.suggests = re;
                        this.searching = false;
                        if(this.keywords==''){
                            this.suggests=[];
                        }
                    }
                }).catch(()=>{
                    console.log("搜索请求失败");
                }).finally(()=>{
                    this.searching=false;
                })
            }
        }
    },
    components:{
        Suggest,
        Loading,
        SearchResult,
        HotSearch,
        MusicItem,
        TopNav
    },
    beforeRouteEnter(to,from,next){
        next(vm=>{
            vm.$http.get('/search/hot/detail').then(data=>{
                vm.hotSearch = data.data.result.hots;
            }).catch(()=>{
                console.log("请求数据失败");
            })
        })
    }
}
</script>


<style lang="less" scoped>
form{
    border-bottom: 2px solid RGB(246,247,247);
    .search {
        margin: 10px;
        padding: 5px 10px;
        display: flex;
        align-items: center;
        background-color: #ebeced;
        border-radius: 20px;
        input {
            border: none;
            outline: none;
            flex: 1;
            background-color: transparent;
        }
        i {
            display: inline-block;
            width: 13px;
            height: 13px;
            &:first-of-type {
                background-image: url(data:image/svg+xml;base64,);
            }
            &:last-of-type {
                background-image: url(data:image/svg+xml;base64,);
            }
        }
    }

}
</style>