<template>
    <div class="hot-search" >
        <p class="title">
            <span>热门搜索</span>
        </p>
        <ul class="music-list">
            <li v-for="item in hotSearch" :key="item.id" @click="search(item.first)">
                <a>{{item.first}}</a>
            </li>
        </ul>
    </div>
</template>
<script>
export default {
    props:["hotSearch"],
    methods:{
        search(searchName){
            //将输入框设置对应值
            this.$emit("clickHotSearch",searchName);
        },
    }
}
</script>
<style lang="less" scoped>

.hot-search {
    .title {
        padding: 10px 5px;
        span {
            margin: 0px 10px;
            color: RGB(102,102,102);
            font-size: 12px;
        }
    }
    .music-list {
        padding: 0px 20px 5px 10px;
        display: flex;
        flex-wrap: wrap;
        li {
            padding: 3px 10px ;
            border: 2px solid RGB(241,242,244);
            border-radius: 20px;
            margin: 5px 3px;
            a {
                color: RGB(51,51,51);
                font-size: 12px;
            }
        }
    }
}
</style>